/**
 * @module M/control/Plg_hor_navigationScale
 */

import Plg_hor_navigationImplScale from 'impl/plg_hor_navigationscale';
import template from 'templates/navigationScale.html';

export default class Plg_hor_navigationScale extends M.Control {
  /**
   * @classdesc
   * Main constructor of the class. Creates a PluginControl
   * control
   *
   * @constructor
   * @extends {M.Control}
   * @api stable
   */
  constructor(map, scaleConfig) {
    // 1. checks if the implementation can create PluginControl
    if (M.utils.isUndefined(Plg_hor_navigationImplScale)) {
      M.exception('La implementación usada no puede crear controles Plg_hor_navigationImplScale');
    }

    const SCALES = [1000, 2000, 5000, 10000, 25000, 50000, 100000, 150000, 250000, 500000, 1000000];

    // 2. implementation of this control
    var scaleC = scaleConfig || {};
    if(M.utils.isNullOrEmpty(scaleConfig) || M.utils.isNullOrEmpty(scaleConfig.scales)){
      scaleC.scales = SCALES;
    }

    const impl = new Plg_hor_navigationImplScale(scaleC);
    super(impl, 'Plg_hor_navigationScale');
    this.scaleConfig = scaleC;
  }

  /**
   * This function creates the view
   *
   * @public
   * @function
   * @param {M.Map} map to add the control
   * @api stable
   */
  createView(map) {
    if (!M.template.compileSync) { // JGL: retrocompatibilidad Mapea4
      M.template.compileSync = (string, options) => {
        let templateCompiled;
        let templateVars = {};
        let parseToHtml;
        if (!M.utils.isUndefined(options)) {
          templateVars = M.utils.extends(templateVars, options.vars);
          parseToHtml = options.parseToHtml;
        }
        const templateFn = Handlebars.compile(string);
        const htmlText = templateFn(templateVars);
        if (parseToHtml !== false) {
          templateCompiled = M.utils.stringToHtml(htmlText);
        } else {
          templateCompiled = htmlText;
        }
        return templateCompiled;
      };
    }
    
    var _this = this;
    return new Promise((success, fail) => {
      const html = M.template.compileSync(template);
      _this.addEvents(html);
      success(html);
    });
  }

  /**
   * This function is called on the control activation
   *
   * @public
   * @function
   * @api stable
   */
  activate() {
    // calls super to manage de/activation
    super.activate();

  }
  /**
   * This function is called on the control deactivation
   *
   * @public
   * @function
   * @api stable
   */
  deactivate() {
    // calls super to manage de/activation
    super.deactivate();

  }
  /**
   * This function gets activation button
   *
   * @public
   * @function
   * @param {HTML} html of control
   * @api stable
   */
  getActivationButton(html) {
    return;
  }

  /**
   * This function compares controls
   *
   * @public
   * @function
   * @param {M.Control} control to compare
   * @api stable
   */
  equals(control) {
    return control instanceof Plg_hor_navigationScale;
  }

  // Add your own functions
  addEvents(element){
    var buttonZoom2Scale = element.getElementsByTagName('select')['m-navigationscale-select'];
    var _this = this;
    buttonZoom2Scale.addEventListener('change', function(e){
      _this.onScaleChange(e, _this);
    });
    this.addScaleOptions(element);
  }

  addScaleOptions(element){
    var buttonZoom2Scale = element.getElementsByTagName('select')['m-navigationscale-select'];
    for(var i = 0; i < this.scaleConfig.scales.length; i++){
      var option = document.createElement('option');
      option.setAttribute('value', this.scaleConfig.scales[i]);
      var txtNode = document.createTextNode('1:' + this.scaleConfig.scales[i]);
      option.appendChild(txtNode);
      buttonZoom2Scale.appendChild(option);
    }
  }

  onScaleChange(e, t){
    var elt = e.target;
    var scale = elt.value;
    t.getImpl().zoomToScale(scale);
  }

}
