/**
 * @module M/impl/Navigation/style
 */

 export default class Plg_hor_navigationstyle {
    
    constructor(){
        M.impl.Navigation = {};
        M.impl.Navigation.style = {};
        M.impl.Navigation.style.initializated = false;
    }

    init(){
        if(M.impl.Navigation.style.initializated === false){
            M.impl.Navigation.style.initializated = true;

            M.impl.Navigation.style.DEFAULT = {};
            M.impl.Navigation.style.DEFAULT[M.geom.wkt.type.POINT] = new ol.style.Style({
                image: new ol.style.RegularShape({
                    fill: new ol.style.Fill({
                        color: '#fcfcfc'
                    }),
                    stroke: new ol.style.Stroke({
                        color: '#3589d1',
                        width: 2
                    }),
                    points: 3,
                    radius: 7,
                    rotation: Math.PI / 3,
                    angle: 0
                })
            });
        }
    }

 }