/**
 * @module M/plugin/PLG_Hor_Navigation
 */
import 'assets/css/navigation';
import Plg_hor_navigationZoomControl from './plg_hor_navigationzoomcontrol';
import Plg_hor_navigationZoomExtent from './plg_hor_navigationzoomextent';
import Plg_hor_navigationZoomHistory from './plg_hor_navigationzoomhistory';
import Plg_hor_navigationCoordinates from './plg_hor_navigationcoordinates';
import Plg_hor_navigationScale from './plg_hor_navigationscale';

import api from '../../api.json'

export default class Plg_hor_navigation extends M.Plugin {
  /**
   * @classdesc
   * Main facade plugin object. This class creates a plugin
   * object which has an implementation Object
   *
   * @constructor
   * @extends {M.Plugin}
   * @param {Object} impl implementation object
   * @api stable
   */
  constructor(userParameters) {
    super();
    if (M.utils.isNullOrEmpty(userParameters)){
      M.exception('No se ha especifado ningún parámetro');
    }


    /**
     * Facade of the map
     * @private
     * @type {M.Map}
     */
    this.map_ = null;

    /**
     * Array of controls
     * @private
     * @type {Array<M.Control>}
     */
    this.controls_ = [];

    /**
     * Metadata from api.json
     * @private
     * @type {Object}
     */
    this.metadata_ = api.metadata;
    
    this.userParameters_ = userParameters;
  }

  /**
   * This function adds this plugin into the map
   *
   * @public
   * @function
   * @param {M.Map} map the map to add the plugin
   * @api stable
   */
  addTo(map) {
    var _this = this;

    this.map_ = map;
    this.addControls();
    this.panel_ = new M.ui.Panel('navigation', {
      collapsible: true,
      position: M.ui.position.TL,
      className: 'plg-navigation',
      collapsedButtonClass: 'g-navigationtools-closed',
      openedButtonClass: 'g-navigationtools-opened',
      tooltip: 'Herramientas de navegación'
    });
    this.panel_.on(M.evt.ADDED_TO_MAP, function(html){
      M.utils.enableTouchScroll(html);
      _this.panel_.open();
    });
    this.panel_.addControls(this.controls_);
    map.addPanels(this.panel_);
  }

  addControls(){
    var measureAreaControl = null;
    var measureLengthControl = null;

    if(this.userParameters_.measureLength){
      this.map_.addControls(new M.control.MeasureLength());

      measureLengthControl = new M.control.MeasureLength();
      this.controls_.push(measureLengthControl);
    }

    if(this.userParameters_.measureArea){
      this.map_.addControls(new M.control.MeasureArea());

      measureAreaControl = new M.control.MeasureArea();
      this.controls_.push(measureAreaControl);
    }

    if(this.userParameters_.measureLength && this.userParameters_.measureArea){
      this.map_.addControls(new M.control.MeasureClear(measureLengthControl, measureAreaControl));

      this.controls_.push(new M.control.MeasureClear(measureLengthControl, measureAreaControl));
    } else if(this.userParameters_.measureLength){
      this.map_.addControls(new M.control.MeasureClear(measureLengthControl, measureLengthControl));

      this.controls_.push(new M.control.MeasureClear(measureLengthControl, measureLengthControl));
    } else if(this.userParameters_.measureArea){
      this.map_.addControls(new M.control.MeasureClear(measureAreaControl, measureAreaControl));

      this.controls_.push(new M.control.MeasureClear(measureAreaControl, measureAreaControl));
    }
  
    if(this.userParameters_.identify.add){
      this.map_.addControls(new M.control.GetFeatureInfo(this.userParameters_.identify.config.format,
        this.userParameters_.identify.config.options));

      this.controls_.push(new M.control.GetFeatureInfo(
        this.userParameters_.identify.config.format,
        this.userParameters_.identify.config.options
      ));
    }

    if (this.userParameters_.catastroSearch.add) {
			var catastroSearchControl = new M.control.catastroSearchButtonControl(this.userParameters_.catastroSearch.config);
			this.controls_.push(catastroSearchControl);
		}

    if (this.userParameters_.geocalc) {
			var geocalcControl = new M.control.geocalcControl({
				options: {
					order: 7
				}
			});
			this.controls_.push(geocalcControl);
		}

    this.controls_.unshift(new Plg_hor_navigationZoomControl());
		this.controls_.unshift(new Plg_hor_navigationZoomExtent());
		this.controls_.unshift(new Plg_hor_navigationZoomHistory());
		
		//Scale selector control
		if (this.userParameters_.scale.add) {
			this.controls_.push(new Plg_hor_navigationScale(this.map_, this.userParameters_.scale.config));
		}

		if (this.userParameters_.coordinatesZoom) {
			this.controls_.push(new Plg_hor_navigationCoordinates());
		}

		if (this.userParameters_.addControls.add) {
			for (const c of this.userParameters_.addControls.controls) this.controls_.push(c);
    }
    
  }

  getControls(){
    return this.controls_;
  }


/**
   * This function gets metadata plugin
   *
   * @public
   * @function
   * @api stable
   */
  getMetadata(){
    return this.metadata_;
  }
}
