/**
 * @module M/control/Plg_hor_navigationcoordinates
 */

import Plg_hor_navigationImplCoordinates from 'impl/plg_hor_navigationcoordinates';
import template from 'templates/navigationCoordinates.html';

export default class Plg_hor_navigationCoordinates extends M.Control {
  /**
   * @classdesc
   * Main constructor of the class. Creates a PluginControl
   * control
   *
   * @constructor
   * @extends {M.Control}
   * @api stable
   */
  constructor() {
    // 1. checks if the implementation can create PluginControl
    if (M.utils.isUndefined(Plg_hor_navigationImplCoordinates)) {
      M.exception('La implementación usada no puede crear controles Plg_hor_navigationImplCoordinates');
    }
    // 2. implementation of this control
    const impl = new Plg_hor_navigationImplCoordinates();
    super(impl, 'Plg_hor_navigationCoordinates');

    this.coordXInput_ = null;
    this.coordYInput_ = null;
  }

  /**
   * This function creates the view
   *
   * @public
   * @function
   * @param {M.Map} map to add the control
   * @api stable
   */
  createView(map) {
    if (!M.template.compileSync) { // JGL: retrocompatibilidad Mapea4
      M.template.compileSync = (string, options) => {
        let templateCompiled;
        let templateVars = {};
        let parseToHtml;
        if (!M.utils.isUndefined(options)) {
          templateVars = M.utils.extends(templateVars, options.vars);
          parseToHtml = options.parseToHtml;
        }
        const templateFn = Handlebars.compile(string);
        const htmlText = templateFn(templateVars);
        if (parseToHtml !== false) {
          templateCompiled = M.utils.stringToHtml(htmlText);
        } else {
          templateCompiled = htmlText;
        }
        return templateCompiled;
      };
    }
    
    var _this = this;
    return new Promise((success, fail) => {
      const html = M.template.compileSync(template);
      _this.addEvents(html);
      success(html);
    });
  }

  /**
   * This function is called on the control activation
   *
   * @public
   * @function
   * @api stable
   */
  activate() {
    // calls super to manage de/activation
    super.activate();

  }
  /**
   * This function is called on the control deactivation
   *
   * @public
   * @function
   * @api stable
   */
  deactivate() {
    // calls super to manage de/activation
    super.deactivate();

  }
  /**
   * This function gets activation button
   *
   * @public
   * @function
   * @param {HTML} html of control
   * @api stable
   */
  getActivationButton(html) {
    return;
  }

  /**
   * This function compares controls
   *
   * @public
   * @function
   * @param {M.Control} control to compare
   * @api stable
   */
  equals(control) {
    return control instanceof Plg_hor_navigationCoordinates;
  }

  // Add your own functions
  addEvents(element){
    var _this = this;
    this.coordXInput_ = element.getElementsByTagName('input')['m-navigation-coordinateX-input'];
    this.coordYInput_ = element.getElementsByTagName('input')['m-navigation-coordinateY-input'];

    var buttonApply = element.getElementsByTagName('button')['m-navigationcoordinatesapply-button'];
    buttonApply.addEventListener('click', ()=>{_this.onCenterApply(this)});

    var buttonClear = element.getElementsByTagName('button')['m-navigationcoordinatesclear-button'];
    buttonClear.addEventListener('click', ()=>{_this.onCenterClear(this)});
  }

  onCenterApply(t){
    t.getImpl().centerByCoords(Number(t.coordXInput_.value), Number(t.coordYInput_.value));
  }

  onCenterClear(t){
    t.coordXInput_.value = "";
    t.coordYInput_.value = "";
    t.getImpl().centerClear();
  }


}
