/**
 * @module M/impl/control/Plg_hor_navigationcoordinates
 */

import Plg_hor_navigationLayer from 'impl/plg_hor_navigationlayer';

export default class Plg_hor_navigationCoordinates extends M.impl.Control {
	/**
	 * This function adds the control to the specified map
	 *
	 * @public
	 * @function
	 * @param {M.Map} map to add the plugin
	 * @param {HTMLElement} html of the plugin
	 * @api stable
	 */
	addTo(map, html) {
		// super addTo - don't delete
		super.addTo(map, html);
		this.map_ = map;
		this.layer_ = new Plg_hor_navigationLayer();
		this.layer_.addTo(map);
		this.olMap = map.getMapImpl();
	}

	centerByCoords(lon, lat){
		this.centerClear();
		if(isNaN(lon) || isNaN(lat)){
			M.dialog.error('El formato numérico introducido en la coordenada no es adecuado');
			return;
		}
		else if(lon!==0 && lat !== 0){
			if(this.containsCoords(lon, lat)){
				var mapImpl = this.map_.getMapImpl();
				var view = mapImpl.getView();
				var center = [lon, lat];
				this.layer_.drawResults(lon, lat);
				var availableResolutions = this.facadeMap_.getResolutions();
				var resolutions = availableResolutions[availableResolutions.length - 2];
				view.animate({
					center: center,
					resolutions: resolutions,
					duration: 500
				});
			} else {
				M.dialog.info('Las coordenadas se encuentran fuera de los límites del mapa');
			}
		} else {
			M.dialog.info('Introduzca unas coordenadas');
		}
	}

	containsCoords(lon, lat){
		var maxExtentOrigin = this.map_.getMaxExtent();
		var maxExtent = [];
		if(maxExtentOrigin.length != 4){
			maxExtent = [maxExtentOrigin.x.min, maxExtentOrigin.x.max, maxExtentOrigin.y.min, maxExtentOrigin.y.max];
		} else {
			maxExtent = maxExtentOrigin;
		}
		var coordinates = [lon, lat];
		return ol.extent.containsCoordinate(maxExtent, coordinates);
	}

	centerClear(){
		this.layer_.clear();
	}

	getLayer(){
		return this.layer_;
	}
}
  