/**
 * @module M/impl/control/Plg_hor_navigationImplZoomHistory
 */

export default class Plg_hor_navigationImplZoomHistory extends M.impl.Control {
  /**
   * This function adds the control to the specified map
   *
   * @public
   * @function
   * @param {M.Map} map to add the plugin
   * @param {HTMLElement} html of the plugin
   * @api stable
   */
  addTo(map, html) {
    // super addTo - don't delete    
    super.addTo(map, html);
    var _this = this;
    this.map_ = map;

    var mapImpl = this.map_.getMapImpl();
    var shouldUpdate = true;

    var updatePermalink = function(){
      if(!shouldUpdate){
        shouldUpdate = true;
        return;
      }

      const view = _this.map_.getMapImpl().getView();
      var center = view.getCenter();
      var hash = '#map=' +
        view.getZoom() + "/" + 
        Math.round(center[0] * 100) / 100 + '/' +
        Math.round(center[1] * 100) / 100;

      var state = {
        center: view.getCenter(),
        resolution: view.getResolution()
      };

      window.history.pushState(state, 'map', hash);

    };

    mapImpl.on('moveend', updatePermalink);
    window.addEventListener('popstate', function(event){
      if(M.utils.isUndefined(event.state) ||event.state === null){
        return;
      }
      const view = _this.map_.getMapImpl().getView();
      view.animate({
        center: event.state.center,
        resolution: event.state.resolution,
        duration: 300
      });
      shouldUpdate = false;
    });

    var buttonPrevious = html.getElementsByTagName('button')['m-navigationzoomprevious-button'];
    buttonPrevious.addEventListener("click", this.onPreviousClick);

    var buttonNext = html.getElementsByTagName("button")["m-navigationzoomnext-button"];
    buttonNext.addEventListener("click", this.onNextClick);


  }

  onPreviousClick(){
    window.history.back();
  }

  onNextClick(){
    window.history.forward();
  }

}
