/**
 * @module M/impl/control/Plg_hor_navigationImplZoomControl
 */

import Plg_hor_navigationControl from 'facade/plg_hor_navigationcontrol';
export default class Plg_hor_navigationImplZoomControl extends M.impl.Control {
  /**
   * This function adds the control to the specified map
   *
   * @public
   * @function
   * @param {M.Map} map to add the plugin
   * @param {HTMLElement} html of the plugin
   * @api stable
   */
  addTo(map, html) {
    // super addTo - don't delete    
    super.addTo(map, html);
    this.map_ = map;
  }

  doZoom(factor){
    var zoomIn, zoomOut;
    if(factor > 1){
      zoomOut = true;
    } else {
      zoomOut = false;
    }

    zoomIn = !zoomOut;
    var mapImpl = this.map_.getMapImpl();
    var view = mapImpl.getView();
    var availableResolutions = view.getResolutions();
    var currentResolution = view.getResolution();
    var resolution = currentResolution;
      if ((zoomOut && availableResolutions.indexOf(currentResolution) !== 0) || zoomIn && availableResolutions.indexOf(currentResolution) != availableResolutions.length - 1) {
        var a = new Plg_hor_navigationControl().closestResolution(availableResolutions, currentResolution);
      if(zoomOut){
        resolution = availableResolutions[availableResolutions.indexOf(a) - 1];
      } else {
        resolution = availableResolutions[availableResolutions.indexOf(a) + 1];
      }
    }

    view.animate({
      center: view.getCenter(),
      resolution: resolution,
      duration: 500
    });
  }
}
