/**
 * @module M/impl/control/Plg_hor_navigationScale
 */
export default class Plg_hor_navigationScale extends M.impl.Control {

	constructor(scaleConfig){
		super();
		this.scaleConfig = scaleConfig;
	}

	/**
	 * This function adds the control to the specified map
	 *
	 * @public
	 * @function
	 * @param {M.Map} map to add the plugin
	 * @param {HTMLElement} html of the plugin
	 * @api stable
	 */
	addTo(map, html) {
		// super addTo - don't delete
		super.addTo(map, html);
		this.map_ = map;

		var _this = this;
		var resolutionsArray = this.scaleConfig.scales.map(function(scale){
			return _this.scaleToResolution(scale);
		});
		this.map_.setResolutions(resolutionsArray.sort(this.sortNumber));
		this.updateSelector(html);
		map.getMapImpl().on('moveend', function(){
			_this.updateSelector(html);
		});
	}

	zoomToScale(scale){
		if(scale !== "0"){
			var mapImpl = this.map_.getMapImpl();
			var view = mapImpl.getView();
			var resolution = this.scaleToResolution(scale);
			view.animate({
				center: view.getCenter(),
				resolution: resolution,
				duration: 500
			});
		}
	}

	scaleToResolution(scale){
		var mapImpl = this.map_.getMapImpl();
		var view = mapImpl.getView();
		var units = view.getProjection().getUnits();
		var dpi = 25.4 / 0.35;
		var mpu = ol.proj.METERS_PER_UNIT[units];
		return scale / (mpu * 39 * dpi);
	}

	resolutionToScale(resolution){
		var mapImpl = this.map_.getMapImpl();
		var view = mapImpl.getView();
		var units = view.getProjection().getUnits();
		var dpi = 25.4 / 0.35;
		var mpu = ol.proj.METERS_PER_UNIT[units];
		return resolution * (mpu * 39 * dpi);
	}

	updateSelector(element){
		var view = this.map_.getMapImpl().getView();
		var scale = Math.round(this.resolutionToScale(view.getResolution()));
		var select = element.querySelector('select');
		for (let i = 0; i < element.getElementsByTagName('option').length; i++){
			var option = element.getElementsByTagName('option')[i];
			if(scale == option.getAttribute("value")){
				select.selectedIndex = i;
				break;
			}
		}
	}

	sortNumber(a, b){
		return b-a;
	}


}
  