/**
 * @module M/impl/layer/Navigation
 */

import Plg_hor_navigationLayerStyle from 'impl/plg_hor_navigationlayerstyle';

export default class Plg_hor_navigationLayer extends M.impl.Control{
  
   constructor(){
      super();
      this.map = null;
      this.style = new Plg_hor_navigationLayerStyle();
   }

   addTo(map){
      this.style.init();
      this.map_ = map;
      this.ol3Layer = new ol.layer.Vector({
         source: new ol.source.Vector({
            'useSpatialIndex': false
         }),
         zIndex: M.impl.Map.Z_INDEX[M.layer.type.WFS] + 999
      });

      this.ol3Layer.setVisible(true);
      this.map_.getMapImpl().addLayer(this.ol3Layer);
   }

   drawResults(x,y){
      var projection = ol.proj.get(this.map_.getProjection().code);

      var features = [];
      var feature = new ol.Feature({
         geometry: new ol.geom.Point([x, y]),
         dataProjection: projection
      });

      features.push(feature);

      this.setStyleFeature_(features, M.style.state.DEFAULT);
      this.ol3Layer.getSource().addFeatures(features);
   }

   clear(){
      this.ol3Layer.getSource().clear();
   }

   destroy(){
      var olMap = this.map_.getMapImpl();
      if(!M.utils.isNullOrEmpty(this.ol3Layer)){
         olMap.removeLayer(this.ol3Layer);
         this.ol3Layer = null;
      }
      this.map_ = null;
   }

   equals(obj){
      var equals = false;
      if(obj instanceof M.impl.layer.Navigation){
         equals = (this.url === obj.url);
         equals = equals && (this.name === obj.name);
      }
      return equals;
   }

   setStyleFeature_(features, state){
      this.style.init();

      if(!M.utils.isArray(features)){
         features = [features];
      }

      features.forEach(function(feature){
         var geometryType = feature.getGeometry().getType();
         if (M.utils.isNullOrEmpty(state) || (state === M.style.state.DEFAULT)){
            feature.setStyle(M.impl.Navigation.DEFAULT);
         } else if (state === M.style.state.NEW) {
            feature.setStyle(M.impl.Navigation.NEW[geometryType]);
         } else if (state === M.style.state.SELECTED) {
            features.setStyle(M.impl.Navigation.SELECTED[geometryType]);
         }
      });
   }
}